import React from 'react';
import PropTypes from 'prop-types';
import { Stage, Rect, Layer } from 'react-konva';
import { makeStyles } from '@material-ui/core';
import { textToBitmap } from './utils';

const useStyles = makeStyles({
  root: {
    backgroundColor: (props) => props.backgroundColor || '#565656',
    borderRadius: 4,
    borderColor: '#333',
    display: 'inline-block',
    padding: 5,
    margin: 5,
  },
});

function MatrixLCD({
  backgroundColor,
  column,
  hasGlow = false,
  pixelColor = 'green',
  pixelCornerRadius,
  pixelSize = 10,
  pixelStatus = [],
  row,
  scrollSpeed = 1000,
  space = 2,
}) {
  const classes = useStyles({ backgroundColor });
  const scale =
    window.innerWidth - 100 < (pixelSize + space) * column
      ? (window.innerWidth - 100) / ((pixelSize + space) * column)
      : 1;
  const pixelRoom = (pixelSize + space) * scale;
  const [scroll, setScroll] = React.useState(0);

  React.useEffect(() => {
    let intervalId = 0;
    if (pixelStatus[0].pixels.length > column) {
      pixelStatus[0].pixels = [
        ...pixelStatus[0].pixels,
        ...[...new Array(column).keys()],
      ];
    }
    pixelStatus.forEach((status, i) => {
      if (status.pixels.length && status.pixels.length > column) {
        status.pixels = [...status.pixels];
        intervalId = setInterval(() => {
          // status.pixels.forEach((r) => r.push(r.shift()));
          pixelStatus[i].pixels.push(pixelStatus[i].pixels.shift());
          setScroll((state) => state + 1);
        }, scrollSpeed);
      }
    });

    return () => {
      if (intervalId) {
        clearInterval(intervalId);
      }
    };
  }, [pixelStatus]);

  const getPixelColor = (r, c) => {
    let color = 'rgba(0, 0, 0, 0.1)';

    pixelStatus.forEach((item) => {
      if (c >= item.x && r >= item.y) {
        if (
          item.pixels[c - item.x] &&
          item.pixels[c - item.x][r] &&
          item.pixels[c - item.x][r].join('') !== '000'
        ) {
          // color = pixelColor;
          color = `rgba(${item.pixels[c - item.x][r]})`;
        }
      }
    });

    return color;
  };

  return (
    <div className={classes.root}>
      <Stage width={pixelRoom * column} height={pixelRoom * row}>
        <Layer x={0} y={0}>
          {[...Array(row).keys()]
            .map((r) =>
              [...Array(column).keys()].map((c) => (
                <Rect
                  key={`${r}-${c}`}
                  cornerRadius={pixelCornerRadius || 0}
                  fill={getPixelColor(r, c)}
                  x={space + pixelRoom * c}
                  y={space + pixelRoom * r}
                  width={pixelSize * scale}
                  height={pixelSize * scale}
                />
              )),
            )
            .flat()}
        </Layer>
      </Stage>
    </div>
  );
}

MatrixLCD.propTypes = {
  backgroundColor: PropTypes.string,
  column: PropTypes.number,
  hasGlow: PropTypes.bool,
  pixelColor: PropTypes.string,
  pixelCornerRadius: PropTypes.number,
  pixelSize: PropTypes.number,
  pixelStatus: PropTypes.array,
  row: PropTypes.number,
  scrollSpeed: PropTypes.number,
  space: PropTypes.number,
};

export default MatrixLCD;
