import React from 'react';
import Card from '@material-ui/core/Card';
import {
  makeStyles,
  Typography,
  CardContent,
  CardActions,
  Button,
} from '@material-ui/core';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import LightbarList from './lightbar-list';
import { getLightbar, getColorSchema } from './lightbar-selector-slice';
import ColorSchemaList from './color-schema-list';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'row',
    alignContent: 'stretch',
    justifyContent: 'center',
    minHeight: 400,
    marginTop: 20,
  },
  card: {
    width: 500,
  },
  subtitle: {
    fontSize: 14,
  },
  button: {
    justifyContent: 'flex-end',
  },
});

export function LightbarSelector() {
  const classes = useStyles();
  const history = useHistory();
  const selectedLightbar = useSelector(getLightbar);
  const selectedColorSchema = useSelector(getColorSchema);
  const routeToLightbar = () => history.push('/lightbar');
  // const routeToLightbar = () => history.push('/lightbar/schema');

  return (
    <div className={classes.root}>
      <Card className={classes.card}>
        <CardContent>
          <Typography variant="h6">Lightbars</Typography>
          <Typography className={classes.subtitle}>
            Select a lightbar model to configure
          </Typography>
          <LightbarList />
          <Typography variant="h6">Color Schema</Typography>
          <Typography className={classes.subtitle}>
            Select the color schema of the lightbar
          </Typography>
          <ColorSchemaList />
        </CardContent>
        <CardActions className={classes.button}>
          <Button
            color="primary"
            disabled={!selectedLightbar || !selectedColorSchema}
            variant="contained"
            onClick={routeToLightbar}
          >
            Start Configurator
          </Button>
        </CardActions>
      </Card>
    </div>
  );
}
