import FileSaver from 'file-saver';

export const toHex = (value) => {
  let hex = Number(value).toString(16);
  if (hex.length < 2) {
    hex = `0${hex}`;
  }

  return hex;
};

export const rgbToHex = (value) =>
  Object.values(value)
    .map((v) => toHex(v))
    .join('');

export const createBinaryBlob = (cycles) => {
  if (!cycles || !cycles.length) {
    return null;
  }

  const lightLength = cycles[0].lights.length;
  const length = 3 + cycles.length * lightLength * 3;
  const buffer = new ArrayBuffer(length);
  const dv = new DataView(buffer);

  dv.setUint8(0, lightLength);
  dv.setUint16(1, cycles.length);

  let index = 3;

  cycles.forEach((c) =>
    c.lights.forEach((l) =>
      [
        (l.color.r * l.color.a).toFixed(0),
        (l.color.g * l.color.a).toFixed(0),
        (l.color.b * l.color.a).toFixed(0),
      ].forEach((v) => dv.setUint8(index++, v)),
    ),
  );

  return new Blob([dv.buffer], { type: 'application/octet-stream' });
};

export const createMessageBoardBinaryBlob = (data) => {
  const buffer = new ArrayBuffer(
    Math.ceil(data.length / 8) * 8 * data[0].length * 3,
  );
  const dv = new DataView(buffer);

  // Reformats the data to be as a area of 8 * 16 pixels
  for (let i = 0; i < Math.ceil(data.length / 8); i += 1) {
    for (let r = 0; r < data[0].length; r += 1) {
      for (let c = 0; c < 8; c += 1) {
        const binaryIndex = i * 8 * data[0].length * 3 + r * 8 + c * 3;
        if (data[i * 8 + c]) {
          data[i * 8 + c][r].forEach((value, index) =>
            dv.setUint8(binaryIndex + index, value),
          );
        } else {
          [0, 0, 0].forEach((value, index) =>
            dv.setUint8(binaryIndex + index, value),
          );
        }
      }
    }
  }

  return new Blob([dv.buffer], { type: 'application/octet-stream' });
};

export const createJSONBlob = (lightbar) =>
  new Blob([JSON.stringify(lightbar)], { type: 'application/json' });

export const saveFile = (blob, name) => {
  FileSaver.saveAs(blob, name);
};

export const getDefaultLights = (lightbar) =>
  [...Array(lightbar.front + lightbar.rear + 6).keys()].map(() => ({
    color: {
      r: 0,
      g: 0,
      b: 0,
      a: 1,
    },
  }));
