import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core';
import { Stage, Layer, Rect, Text } from 'react-konva';

import { rgbToHex } from '../../app/utils';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'row',
    height: (props) => (props.width ? 'auto' : 200),
    justifyContent: 'center',
    marginLeft: 10,
    maxWidth: 750,
    minWidth: (props) => props.width || 500,
    width: (props) => props.width || '100%',
  },
});

const Light = ({
  fill,
  getStrokeColor,
  height,
  onClick,
  overlay,
  rotation,
  scale,
  shadowBlur,
  stroke,
  text,
  width,
  x,
  y,
}) => (
  <>
    <Rect
      fill={fill}
      height={height}
      cornerRadius={5}
      onClick={onClick}
      rotation={rotation}
      shadowBlur={shadowBlur}
      stroke={stroke}
      width={width}
      x={x}
      y={y}
    />
    {text && (
      <Text
        align="center"
        fontSize={10 * scale}
        height={height}
        rotation={rotation}
        text={text}
        verticalAlign="middle"
        width={width}
        x={x}
        y={y}
      />
    )}
  </>
);

function LightbarPreview({
  lights = [],
  onClick,
  selectedLight,
  title,
  width,
}) {
  const classes = useStyles({ width });
  const containerRef = useRef();

  if (!lights.length) {
    return null;
  }

  const front = lights.slice(0, (lights.length - 6) / 2);
  const rear = lights.slice(front.length + 3, -3);
  const originalWidth = 57 * 2 + front.length * 45;
  const scale = (width || originalWidth) / originalWidth;
  const lightbarWidth = front.length * (45 * scale);
  const shadowBlur = 5 * scale;

  // TODO it doesn't work properly
  // useEffect(() => {
  //   if (containerRef.current.offsetWidth !== divWidth
  //     && containerRef.current.offsetWidth <= 750) {
  //       setDivWidth(containerRef.current.offsetWidth);
  //     }
  //   },
  // );

  const onLightClick = (id) => () => onClick && onClick(id);

  const getStrokeColor = (id) => (selectedLight === id ? 'red' : null);

  const getColor = ({ r, g, b, a }) =>
    `#${rgbToHex({
      r: (r * a).toFixed(0),
      g: (g * a).toFixed(0),
      b: (b * a).toFixed(0),
    })}`;

  return (
    <div ref={containerRef} className={classes.root}>
      <Stage width={originalWidth * scale} height={200 * scale}>
        <Layer x={57 * scale}>
          {front.map((l, index) => (
            <Light
              fill={getColor(l.color)}
              height={24 * scale}
              index={index}
              key={index}
              stroke={getStrokeColor(index)}
              text={l.text}
              onClick={onLightClick(index)}
              scale={scale}
              shadowBlur={shadowBlur}
              width={40 * scale}
              x={45 * index * scale}
              y={20 * scale}
            />
          ))}
        </Layer>
        <Layer
          rotation={-180}
          x={57 * 2 * scale + lightbarWidth}
          y={184 * scale}
        >
          <Light
            x={58 * scale}
            y={44 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[front.length + 2].color)}
            rotation={135}
            text={lights[front.length + 2].text}
            shadowBlur={shadowBlur}
            cornerRadius={5}
            onClick={onLightClick(front.length + 2)}
            stroke={getStrokeColor(front.length + 2)}
            scale={scale}
          />
          <Light
            x={30 * scale}
            y={73 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[front.length + 1].color)}
            shadowBlur={shadowBlur}
            text={lights[front.length + 1].text}
            rotation={90}
            cornerRadius={5}
            onClick={onLightClick(front.length + 1)}
            stroke={getStrokeColor(front.length + 1)}
            scale={scale}
          />
          <Light
            x={30 * scale}
            y={113 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[front.length].color)}
            shadowBlur={shadowBlur}
            text={lights[front.length].text}
            rotation={45}
            cornerRadius={5}
            onClick={onLightClick(front.length)}
            stroke={getStrokeColor(front.length)}
            scale={scale}
          />
        </Layer>
        <Layer x={57 * scale}>
          {rear.map((l, index) => (
            <Light
              key={index}
              x={45 * index * scale}
              y={140 * scale}
              width={40 * scale}
              height={24 * scale}
              fill={getColor(lights[lights.length - 4 - index].color)}
              shadowBlur={shadowBlur}
              text={lights[lights.length - 4 - index].text}
              cornerRadius={5}
              onClick={onLightClick(lights.length - 4 - index)}
              stroke={getStrokeColor(lights.length - 4 - index)}
              scale={scale}
            />
          ))}
        </Layer>
        <Layer>
          <Light
            x={55 * scale}
            y={44 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[lights.length - 1].color)}
            text={lights[lights.length - 1].text}
            shadowBlur={shadowBlur}
            rotation={135}
            cornerRadius={5}
            onClick={onLightClick(lights.length - 1)}
            stroke={getStrokeColor(lights.length - 1)}
            scale={scale}
          />
          <Light
            x={27 * scale}
            y={73 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[lights.length - 2].color)}
            text={lights[lights.length - 2].text}
            shadowBlur={shadowBlur}
            rotation={90}
            cornerRadius={5}
            onClick={onLightClick(lights.length - 2)}
            stroke={getStrokeColor(lights.length - 2)}
            scale={scale}
          />
          <Light
            x={27 * scale}
            y={113 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[lights.length - 3].color)}
            text={lights[lights.length - 3].text}
            shadowBlur={shadowBlur}
            rotation={45}
            cornerRadius={5}
            onClick={onLightClick(lights.length - 3)}
            stroke={getStrokeColor(lights.length - 3)}
            scale={scale}
          />
        </Layer>
      </Stage>
    </div>
  );
}

LightbarPreview.propTypes = {
  lights: PropTypes.array,
  onClick: PropTypes.func,
  selectedLight: PropTypes.number,
  title: PropTypes.node,
  width: PropTypes.number,
};

export default LightbarPreview;
