export const getTextWidth = (text, font) => {
  const canvas = document.createElement('canvas');
  const ctx = canvas.getContext('2d');
  ctx.font = font;
  return ctx.measureText(text).width;
};

export const convertToRowBaseArray = (dataImage) => {
  const data = [];

  for (let c = 0; c < dataImage.width; c += 1) {
    const col = [];
    for (let r = 0; r < dataImage.height; r += 1) {
      const pixel = r * dataImage.width * 4 + c * 4;
      col.push([
        // dataImage.data[pixel],
        // dataImage.data[pixel + 1],
        // dataImage.data[pixel + 2],
        dataImage.data[pixel] < 100 ? 0 : dataImage.data[pixel],
        dataImage.data[pixel + 1] < 100 ? 0 : dataImage.data[pixel + 1],
        dataImage.data[pixel + 2] < 100 ? 0 : dataImage.data[pixel + 2],
        // dataImage.data[pixel + 3],
      ]);
    }
    data.push(col);
  }

  return data;
};

export const convertTextToBitmap = (text, userStyle) => {
  if (!text || text.trim() === '') {
    return [];
  }
  const canvas = document.createElement('canvas');
  const style = {
    ...{
      fontFamily: 'Open Sans',
      fontSize: 14,
      fontStyle: 'bold',
      textColor: '#00ff00',
      backgroundColor: 'rgba(0, 0, 0)',
    },
    ...userStyle,
  };
  const font = `${style.fontStyle} ${style.fontSize}px ${style.fontFamily}`;
  console.log(font);
  canvas.width = getTextWidth(text, font).toFixed(0);
  canvas.height = 16;
  const ctx = canvas.getContext('2d');
  ctx.textBaseline = 'middle';
  ctx.fillStyle = style.backgroundColor;
  ctx.fillRect(0, 0, canvas.width, canvas.height);
  ctx.font = font;
  ctx.fillStyle = style.textColor;
  ctx.fillText(text, 0, 8);
  return convertToRowBaseArray(
    ctx.getImageData(0, 0, canvas.width, canvas.height),
  );
};
