import React, { useEffect, useState } from 'react';
import {
  AppBar,
  Toolbar,
  IconButton,
  makeStyles,
  Typography,
  Button,
  MenuItem,
  Menu,
} from '@material-ui/core';
// import MenuIcon from '@material-ui/icons/Menu';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import HomeIcon from '@material-ui/icons/Home';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import { useLocation, useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { saveAsFile, setShowPreview } from '../features/Lightbar/ligther-slice';
import { exportData } from '../features/ScrollingBoard/scrolling-board-slice';

const useStyles = makeStyles((theme) => ({
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  previewButton: {
    color: '#ffffff',
    borderColor: '#ffffff',
  },
}));

function Header() {
  const classes = useStyles();
  const location = useLocation();
  const history = useHistory();
  const [showAppbarButtons, setShowAppbarButton] = useState({
    showPreview: false,
    showExport: false,
  });
  const [anchorEl, setAnchorEl] = React.useState(null);
  const dispatch = useDispatch();

  const onCloseContexMenu = (isJSON) => () => {
    if (location.pathname.search(/lightbar$/) > -1) {
      if (isJSON !== undefined) {
        dispatch(saveAsFile(isJSON));
      }
    } else if (location.pathname.search(/message-board/) > -1) {
      if (isJSON !== undefined) {
        dispatch(exportData(isJSON));
      }
    }
    setAnchorEl(null);
  };
  const openContextMenu = (e) => setAnchorEl(e.currentTarget);
  const onPreviewClick = () => dispatch(setShowPreview(true));
  const goHome = () => history.replace('/');

  useEffect(() => {
    if (location.pathname.search(/lightbar$/) > 0) {
      setShowAppbarButton({ showPreview: true, showExport: true });
    } else if (location.pathname.search(/message-board/) > 0) {
      setShowAppbarButton({ showPreview: false, showExport: true });
    } else {
      setShowAppbarButton({ showPreview: false, showExport: false });
    }
  }, [location]);

  return (
    <AppBar position="static">
      <Toolbar>
        <IconButton
          edge="start"
          color="inherit"
          aria-label="Menu"
          onClick={goHome}
        >
          <HomeIcon />
        </IconButton>
        <Typography variant="h6" className={classes.title}>
          DandR Electronic Lighbar Configurator
        </Typography>
        {showAppbarButtons.showPreview && (
          <Button
            className={classes.previewButton}
            onClick={onPreviewClick}
            startIcon={<PlayArrowIcon />}
            variant="outlined"
          >
            Preview
          </Button>
        )}
        {showAppbarButtons.showExport && (
          <IconButton
            aria-label="Download"
            color="inherit"
            edge="end"
            onClick={openContextMenu}
          >
            <MoreVertIcon />
          </IconButton>
        )}
        <Menu
          id="simple-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={onCloseContexMenu()}
        >
          <MenuItem onClick={onCloseContexMenu(false)}>Export BIN</MenuItem>
          <MenuItem onClick={onCloseContexMenu(true)}>Export JSON</MenuItem>
        </Menu>
      </Toolbar>
    </AppBar>
  );
}

export default Header;
