import React from 'react';
import PropTypes from 'prop-types';
import {
  Toolbar,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  makeStyles,
  IconButton,
} from '@material-ui/core';
import InsertEmoticonIcon from '@material-ui/icons/InsertEmoticon';
import { Picker } from 'emoji-mart';
import 'emoji-mart/css/emoji-mart.css';
import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
  emojiIcon: {
    fontSize: 30,
  },
  emojiPicker: {
    position: 'absolute',
    zIndex: 999,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  toolbar: {
    flexWrap: 'wrap',
    padding: 'unset',
  },
}));

const FontToolbar = ({
  addEmoji = () => null,
  className,
  fontFamily = 'Open Sans',
  fontSize = 14,
  fontStyle = 'bold',
  onChange = () => null,
}) => {
  const classes = useStyles();
  const [styles, setStyles] = React.useState({
    fontFamily,
    fontSize,
    fontStyle,
  });
  const [isEmojiPickerVisible, setEmojiPickerVisibility] = React.useState(
    false,
  );
  const onFontChange = (item) => (e) => {
    setStyles({
      ...styles,
      [item]: e.target.value,
    });
    onChange({
      ...styles,
      [item]: e.target.value,
    });
  };
  const selectEmoji = (emoji) => {
    setEmojiPickerVisibility(false);
    addEmoji(emoji);
  };

  return (
    <Toolbar className={clsx(classes.toolbar, className)}>
      <FormControl variant="outlined" className={classes.formControl}>
        <InputLabel id="font-family">Font</InputLabel>
        <Select
          edge="start"
          labelId="font-family"
          label="Font"
          onChange={onFontChange('fontFamily')}
          value={styles.fontFamily}
        >
          <MenuItem value="Roboto">Roboto</MenuItem>
          <MenuItem value="Roboto Mono">Roboto Mono</MenuItem>
          <MenuItem value="Open Sans">Open Sans</MenuItem>
          <MenuItem value="Baloo Bhaina 2">Baloo Bhaina</MenuItem>
          <MenuItem value="Montserrat">Montserrat</MenuItem>
          <MenuItem value="Titillium Web">Titillium Web</MenuItem>
          <MenuItem value="Cairo">Cairo</MenuItem>
        </Select>
      </FormControl>
      <FormControl variant="outlined" className={classes.formControl}>
        <InputLabel id="font-size">Size</InputLabel>
        <Select
          labelId="font-size"
          label="Size"
          onChange={onFontChange('fontSize')}
          value={styles.fontSize}
        >
          <MenuItem value={6}> 6 px</MenuItem>
          <MenuItem value={8}> 8 px</MenuItem>
          <MenuItem value={10}> 10 px</MenuItem>
          <MenuItem value={12}> 12 px</MenuItem>
          <MenuItem value={14}> 14 px</MenuItem>
          <MenuItem value={16}> 16 px</MenuItem>
        </Select>
      </FormControl>
      <IconButton
        arial-label="Emoji"
        onClick={() => setEmojiPickerVisibility(!isEmojiPickerVisible)}
      >
        <InsertEmoticonIcon className={classes.emojiIcon} />
      </IconButton>
      {isEmojiPickerVisible && (
        <div className={classes.emojiPicker}>
          <Picker
            onSelect={selectEmoji}
            style={{ width: 'unset', maxWidth: 345 }}
          />
        </div>
      )}
    </Toolbar>
  );
};

FontToolbar.propTypes = {
  addEmoji: PropTypes.func,
  className: PropTypes.string,
  fontFamily: PropTypes.string,
  fontSize: PropTypes.string,
  fontStyle: PropTypes.string,
  onChange: PropTypes.func,
};

export default FontToolbar;
