import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Card, CardContent } from '@material-ui/core';
import { Redirect } from 'react-router-dom';
import { getDefaultLights } from '../../app/utils';
import { getLightbar } from '../LightbarSelector/lightbar-selector-slice';
import LightbarPreview from '../LightbarPreview';
import { setLightbar, getName, getSchema } from './lightbar-shema-slice';

const LightbarSchema = () => {
  const lightbar = useSelector(getLightbar);
  const dispose = useDispatch();
  const name = useSelector(getName);
  const shema = useSelector(getSchema);

  React.useEffect(() => {
    dispose(setLightbar(lightbar));
  }, []);

  if (!lightbar) {
    return <Redirect to="/lightbars" />;
  }

  const lights = getDefaultLights(lightbar);
  const handleClick = (id) => console.log(id);
  return (
    <Card>
      <CardContent>
        <LightbarPreview lights={lights} onClick={handleClick} />
      </CardContent>
    </Card>
  );
};

LightbarSchema.propTypes = {};

export default LightbarSchema;
