// eslint-disable no-parameter-reassign
import { createSlice } from '@reduxjs/toolkit';
import { convertTextToBitmap } from './helpers';
import {
  createMessageBoardBinaryBlob,
  saveFile,
  createJSONBlob,
} from '../../app/utils';

const slice = createSlice({
  name: 'scrollingBoard',
  initialState: {
    column: 48,
    firstLine: 'Hello World    ',
    imageData: convertTextToBitmap('Hello World    '),
    ledColor: { r: 0, g: 255, b: 0, a: 1 },
    pixelCornerRadius: 0,
    pixelSize: 10,
    row: 16,
    secondLine: 'Line 2',
    scrollSpeed: 50,
    spaceBetweenPixels: null,
    style: null,
  },
  reducers: {
    setColumn: (state, action) => {
      state.column = action.payload;
    },
    setImageData: (state, action) => {
      state.imageData = action.payload;
    },
    setFirstLine: (state, action) => {
      state.firstLine = action.payload;
    },
    setLedColor: (state, action) => {
      state.ledColor = action.payload;
    },
    setPixelCornerRadius: (state, action) => {
      state.pixelCornerRadius = action.payload;
    },
    setRow: (state, action) => {
      state.row = action.payload;
    },
    setSecondLine: (state, action) => {
      state.secondLine = action.payload;
    },
    setScrollSpeed: (state, action) => {
      state.scrollSpeed = action.payload;
    },
    setBoardState: (state, action) => action.payload,
    exportData: (state, action) => {
      const [blob, type] = action.payload
        ? [createJSONBlob(state.imageData), 'json']
        : [createMessageBoardBinaryBlob(state.imageData), 'bin'];
      saveFile(blob, `message-board-${new Date().getTime()}.${type}`);
    },
  },
});

export const {
  exportData,
  setColumn,
  setFirstLine,
  setImageData,
  setLedColor,
  setRow,
  setSecondLine,
  setScrollSpeed,
  setBoardState,
} = slice.actions;

export const getBoardState = (state) => state.scrollingBoard;
export const getColumn = (state) => state.scrollingBoard.column;
export const getFirstLine = (state) => state.scrollingBoard.firstLine;
export const ggetImageData = (state) => state.scrollingBoard.imageData;
export const getLedColor = (state) => state.scrollingBoard.ledColor;
export const getRow = (state) => state.scrollingBoard.row;
export const getSecondLine = (state) => state.scrollingBoard.secondLine;
export const getScrollSpeed = (state) => state.scrollingBoard.scrollSpeed;

export default slice.reducer;
