import React from 'react';
import {
  Card,
  CardContent,
  makeStyles,
  CardHeader,
  TextField,
  Button,
  InputAdornment,
} from '@material-ui/core';
import Bmp from 'binary-bmp';
import CloseIcon from '@material-ui/icons/Close';
import { useSelector, useDispatch } from 'react-redux';
import clsx from 'clsx';
import html2canvas from 'html2canvas';
import MatrixLCD from '../MatrixLCD';
import { textToBitmap } from '../MatrixLCD/utils';
import ColorPicker from '../Lightbar/LightbarSetting/color-picker';
import {
  getBoardState,
  setLedColor,
  setBoardState,
} from './scrolling-board-slice';
import { convertTextToBitmap } from './helpers';
import FontToolbar from './font-toolbar';

const useStyles = makeStyles({
  root: {
    alignItems: 'center',
    display: 'flex',
    flexDirection: 'column',
  },
  matrixLCD: {
    marginTop: 20,
    textAlign: 'center',
  },
  card: {
    width: '60vw',
    marginTop: 20,
  },
  cardContent: {
    display: 'flex',
    flexDirection: 'column',
  },
  row: {
    alignItems: 'center',
    display: 'flex',
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    margin: '5px 0',
  },
  textField: {
    margin: '5px 5px',
  },
  text: {
    margin: '5px 5px',
    '& input': {
      fontFamily: (props) =>
        (props.style && props.style.fontFamily) || 'Roboto',
      fontSize: (props) => (props.style && props.style.fontSize) || 16,
    },
  },
  fullwidth: {
    width: '100%',
  },
  colorView: {
    background: (props) => `rgba(${Object.values(props.ledColor)})`,
    height: 25,
    width: 25,
  },
});

function ScrollingBoard() {
  const dispatch = useDispatch();
  const configs = useSelector(getBoardState);
  const classes = useStyles(configs);
  // const pixelStatus = [
  //   { x: 0, y: 0, pixels: textToBitmap(configs.firstLine, 1) },
  //   { x: 0, y: 8, pixels: textToBitmap(configs.secondLine, 1) },
  // ];
  const pixelStatus = [{ x: 0, y: 0, pixels: configs.imageData }];

  const [showColorPicker, setShowColorPicker] = React.useState(false);
  const [textStyles, setTextStyle] = React.useState();

  const setColorPickerState = () => setShowColorPicker(!showColorPicker);

  const onColorPickerChange = (color) => {
    dispatch(setLedColor(color.rgb));

    const style = { ...configs.style, textColor: color.hex };
    dispatch(
      setBoardState({
        ...configs,
        style,
        imageData: convertTextToBitmap(configs.firstLine, style),
      }),
    );
  };

  const onChange = (e) =>
    dispatch(setBoardState({ ...configs, [e.target.name]: +e.target.value }));

  const onChangeText = (e) => {
    dispatch(
      setBoardState({
        ...configs,
        [e.target.name]: e.target.value,
        imageData: convertTextToBitmap(e.target.value),
      }),
    );
  };

  const changeStyle = (style) => {
    setTextStyle(style);
    dispatch(
      setBoardState({
        ...configs,
        style,
        imageData: convertTextToBitmap(configs.firstLine, style),
      }),
    );
  };

  const addEmoji = (emoji) =>
    dispatch(
      setBoardState({
        ...configs,
        firstLine: `${configs.firstLine} ${emoji.native}`,
        imageData: convertTextToBitmap(
          `${configs.firstLine} ${emoji.native}`,
          textStyles,
        ),
      }),
    );

  return (
    <div className={classes.root}>
      <div className={classes.matrixLCD}>
        <MatrixLCD
          column={configs.column}
          pixelColor={`rgba(${Object.values(configs.ledColor)})`}
          pixelCornerRadius={configs.pixelCornerRadius}
          pixelStatus={pixelStatus}
          scrollSpeed={configs.scrollSpeed}
          row={configs.row}
        />
      </div>
      <Card className={classes.card}>
        <CardHeader title="Message Board Setting" />
        <CardContent className={classes.cardContent}>
          <div className={classes.row}>
            <TextField
              className={classes.textField}
              label="Row"
              inputProps={{ max: 200, min: 0 }}
              name="row"
              onChange={onChange}
              size="small"
              type="number"
              value={configs.row}
              variant="outlined"
            />
            <CloseIcon />
            <TextField
              className={classes.textField}
              label="Column"
              inputProps={{ max: 200, min: 0 }}
              name="column"
              onChange={onChange}
              size="small"
              type="number"
              value={configs.column}
              variant="outlined"
            />
            <TextField
              className={classes.textField}
              label="Pixel corner"
              inputProps={{ max: 360, min: 0 }}
              name="pixelCornerRadius"
              onChange={onChange}
              size="small"
              type="number"
              value={configs.pixelCornerRadius}
              variant="outlined"
            />
            <TextField
              className={classes.textField}
              label="Scroll Interval"
              inputProps={{ max: 1000, min: 0 }}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">ms</InputAdornment>
                ),
              }}
              name="scrollSpeed"
              onChange={onChange}
              size="small"
              type="number"
              value={configs.scrollSpeed}
              variant="outlined"
            />
          </div>
          <div className={classes.row}>
            <TextField
              className={classes.textField}
              label="Red"
              inputProps={{ max: 255, min: 0 }}
              name="red"
              onChange={onChange}
              size="small"
              type="number"
              value={configs.ledColor.r}
              variant="outlined"
              disabled
            />
            <TextField
              className={classes.textField}
              label="Green"
              inputProps={{ max: 255, min: 0 }}
              name="green"
              onChange={onChange}
              size="small"
              type="number"
              value={configs.ledColor.g}
              variant="outlined"
              disabled
            />
            <TextField
              className={classes.textField}
              label="Blue"
              inputProps={{ max: 255, min: 0 }}
              name="blue"
              onChange={onChange}
              size="small"
              type="number"
              value={configs.ledColor.b}
              variant="outlined"
              disabled
            />
            <Button
              color="default"
              onClick={setColorPickerState}
              variant="contained"
            >
              <span className={classes.colorView} />
            </Button>
            {showColorPicker && (
              <ColorPicker
                onClick={setColorPickerState}
                onChange={onColorPickerChange}
                value={configs.ledColor}
              />
            )}
          </div>
          <div className={classes.row}>
            <FontToolbar
              className={classes.fullwidth}
              onChange={changeStyle}
              addEmoji={addEmoji}
            />
            <TextField
              className={clsx([classes.text, classes.fullwidth])}
              label="First Line"
              name="firstLine"
              onChange={onChangeText}
              size="small"
              type="text"
              value={configs.firstLine}
              variant="outlined"
            />
          </div>
          {/*
          <div className={classes.row}>
            <TextField
              className={clsx([classes.textField, classes.fullwidth])}
              label="Second Line"
              name="secondLine"
              onChange={onChangeText}
              size="small"
              type="text"
              value={configs.secondLine}
              variant="outlined"
            />
          </div>
          */}
          <canvas className={classes.canvas} id="hidden-canvas">
            Your browser doesn't support canvas
          </canvas>
        </CardContent>
      </Card>
    </div>
  );
}

export default ScrollingBoard;
